.clients {
  margin: 0 40px 556px 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.clients__title {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 38px;
  line-height: 46px;
  text-transform: uppercase;
  color: #B68F33;
  text-align: center;
  padding-bottom: 15px;
  border-bottom: 1px solid #FFFFFF;
  width: max-content;
  margin-bottom: 72px;
}

.clients__logos {
  display: grid;
  grid-template-columns: repeat(12, 100px);
  grid-template-rows: 78px;
  grid-auto-rows: 78px;
  justify-items: center;
  align-items: center;
  margin-bottom: 80px;
}

.clients__image {
  object-fit: cover;
}

.clients__overlay {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.clients__overlay-line {
  width: 134px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.6);
  margin-bottom: 19px;
}

.clients__subtitle {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.6);
}
