.searchbar {
  padding: 40px;
  visibility: visible;
  position: absolute;
  width: 1200px;
  bottom: 40px;
  left: 50%;
  transform: translate(-50%, 0);
  /*display: flex;
  justify-content: space-between;
  align-items: center;*/
  display: grid;
  grid-template-columns: 1fr max-content;
}

.searchbar__links {
  display: grid;
  grid-template-columns: repeat(4, max-content);
  column-gap: 17px;
  grid-area: 1 / 1 / 2 / 2;
}

.searchbar__link-image {
  width: 24px;
  height: 24px;
}

.searchbar__input {
  width: 482px;
  padding: 14px 10px;
  grid-area: 1 / 1 / 2 / 2;
  border: none;
  background-color: inherit;
  border-bottom: 1px solid #B68F33;
  outline: none;
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: left;
  color: rgba(255, 255, 255, 0.5);
}

.searchbar__buttons {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  column-gap: 34px;
  grid-area: 1 / 2 / 2 / 3;
}

.searchbar__button {
  background-color: inherit;
  border: 2px solid #B68F33;
  border-radius: 20px;
  padding: 14px 22px;
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: #B68F33;
}

.searchbar__button_padding {
  padding: 14px 29px;
}

.searchbar__button_1 {
  grid-area: 1 / 1 / 2 / 2;
}

.searchbar__button_2 {
  grid-area: 1 / 2 / 2 / 3;
}


.searchbar__button_3 {
  grid-area: 1 / 1 / 2 / 2;
  transform: translate(0, 100px);
}

.searchbar__search {
  column-gap: 148px;
}

.searchbar__search-li {
  display: flex;
  align-items: center;

}

.searchbar__search-text {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
}

.searchbar__search-image {
  width: 14px;
  height: 7px;
  margin-left: 17px;
}