.menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  visibility: visible;
}

.menu__column {
  position: absolute;
  top: 0;
  height: 720px;
  left: 50%;
  transform: translate(-50%, 0);
}

.menu__column_1 {
  visibility: hidden;
  z-index: 2;
}

.menu__column_2 {
  z-index: 1;
}

.menu__background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
}

.menu__links {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: grid;
  /*grid-template-columns: repeat(4, min-content);*/
  grid-template-columns: 183px 123px 71px 137px;
  column-gap: 174px;
  justify-items: center;
  align-items: center;
}

.menu__link {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 34px;
  text-transform: uppercase;
  color: #B68F33;
}

.menu__link:last-of-type {
  margin-right: 0;
}

.menu__link_1 {
  grid-area: 1 / 1 / 2 / 2;
}

.menu__link_2 {
  grid-area: 1 / 2 / 2 / 3;
}

.menu__link_3 {
  grid-area: 1 / 3 / 2 / 4;
}

.menu__link_4 {
  grid-area: 1 / 4 / 2 / 5;
}

.menu__link_inactive {
  color: rgba(182, 143, 51, 0.4);
}

.menu__circle {
  grid-area: 1 / 1 / 2 / 2;
  animation: logo-background 100s infinite reverse;
}

.menu__button-open {
  position: absolute;
  top: 61px;
  right: 25px;
  padding: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s;
}

.menu__button-open-point {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #B68F33;
  margin-right: 7px;
}

.menu__button-open-text {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  text-transform: uppercase;
}

.menu__button-close {
  position: absolute;
  top: 58px;
  right: 50px;
  width: 56px;
  height: 56px;
  background-color: #B68F33;
  border-radius: 100%;
  box-sizing: border-box;
  display: grid;
  transition: 1s;
}

.menu__button-close-img {
  box-sizing: border-box;
  align-self: center;
  justify-self: center;
  width: 28px;
  height: 28px;
}

.menu__logo {
  position: absolute;
  top: 50px;
  left: 41px;
  width: 66px;
  height: 65px;
  visibility: visible;
}

