.experience {
  margin: 0 38px 255px 38px;
  display: grid;
  grid-template-columns: 515px 1fr;
  grid-template-rows: 18px 1fr;
  column-gap: 78px;
  row-gap: 83px;
}

.experience__title {
  align-self: start;
  justify-self: start;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: #B68F33; 
}

.experience__texts {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;
  display: grid;
  grid-template-rows: min-content 1fr;
}

.experience__texts-column {
  display: grid;
  grid-template-columns: 291px 1fr;
  column-gap: 51px;
  padding-bottom: 31px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}

.experience__texts-additional {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.8);
}

.experience__texts-main {
  align-self: end;
  justify-self: end;
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 26px;
  color: rgba(255, 255, 255, 0.9);
}