.main {
  height: 720px;
  position: relative;
  margin: 0 0 352px 0;
}

.main__title {
  position: absolute;
  top: 276px;
  left: 208px;
  right: 219px;
  display: grid;
  grid-template-columns: max-content 1fr;
  column-gap: 27px;
  justify-items: center;
  align-items: center;
}

.main__title-text {
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 98px;
  line-height: 92px;
  /*line-height: 119px;*/
  text-transform: uppercase;
  transition: 1s;
}

.main__title-text_position_1 {
  position: absolute;
  top: 0;
  right: -93px;
}

.main__title-text_position_2 {
  position: absolute;
  top: 0;
  left: 174px;
  opacity: 0;
}

.main__title-text_color_gold {
  color: #B68F33;
}

.main__title-line {
  justify-self: start;
  width: 200px;
  height: 5px;
  background-color: #B68F33;
}

.main__title-overlay {
  grid-column-start: 1;
  grid-column-end: 3;
  justify-self: end;
  position: relative;
  width: 100%;
  height: 88px;
}

.main__side-text {
  position: absolute;
  width: 720px;
  height: 47px;
  top: 336px;
  left: -151px;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 54px;
  line-height: 47px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(182, 143, 51, 0.4);
  transform: rotate(-90deg);
  z-index: -1;
}

.main__upper-text {
  position: absolute;
  top: 75px;
  left: 534px;
  font-family: Audrey, Arial, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.2);
  z-index: -1;
}

.main__info {
  position: absolute;
  bottom: 60px;
  left: 40px;
  right: 40px;
  display: grid;
  grid-template-columns: 342px 281px 1fr;
  column-gap: 40px;
  align-items: end;
}

.main__info-text {
  font-family: Inter, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  text-align: left;  
  opacity: 0.8;
}

.main__info-text_position {
  width: min-content;
  justify-self: end;
  text-align: right;
}

@keyframes logo-background {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main__logo-background {
  position: absolute;
  width: 735px;
  height: 735px;
  top: 267px;
  left: 637px;
  animation: logo-background 180s infinite;
  z-index: -1;
  opacity: 0.1;
}

